@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  height: 100vh;
  background: #adff2f;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 400px;
  height: 105px;
  background: #fff;
  border-radius: 18px;
  padding: 28px 32px;
  font-family: Roboto, sans-serif;
  transition: all .6s ease-out;
  position: relative;
  overflow: hidden;
}

.search-box {
  width: 100%;
  height: min-content;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.search-box input {
  color: green;
  width: 80%;
  text-transform: capitalize;
  border: 0;
  border-bottom: 2px solid green;
  outline: none;
  padding-left: 40px;
  font-size: 18px;
  font-weight: 500;
}

.search-box input::placeholder {
  color: green;
  opacity: .5;
  text-transform: capitalize;
  font-size: 18px;
  font-weight: 500;
}

.search-box button {
  cursor: pointer;
  width: 50px;
  height: 50px;
  color: green;
  background: #dff6ff;
  border: none;
  border-radius: 50%;
  font-size: 22px;
  transition: all .4s;
}

.search-box button:hover {
  color: #fff;
  background: green;
}

.search-box i {
  color: green;
  padding-bottom: 5px;
  font-size: 24px;
  position: absolute;
}

.weather-box {
  text-align: center;
}

.weather-box img {
  width: 60%;
  margin-top: 30px;
}

.weather-box .temperature {
  color: green;
  margin-top: 30px;
  margin-left: -16px;
  font-size: 4rem;
  font-weight: 800;
  position: relative;
}

.weather-box .temperature span {
  margin-left: 4px;
  font-size: 1.5rem;
  position: absolute;
}

.weather-box .description {
  color: green;
  text-transform: capitalize;
  font-size: 22px;
  font-weight: 500;
}

.weather-details {
  width: 100%;
  justify-content: space-between;
  margin-top: 15px;
  display: flex;
}

.weather-details .humidity, .weather-details .wind {
  width: 50%;
  height: 100px;
  align-items: center;
  display: flex;
}

.weather-details .humidity {
  justify-content: flex-start;
  padding-left: 20px;
}

.weather-details .wind {
  justify-content: flex-end;
  padding-right: 20px;
}

.weather-details i {
  color: green;
  margin-top: 6px;
  margin-right: 10px;
  font-size: 26px;
}

.weather-details .text {
  padding-left: 10px;
}

.weather-details span {
  color: green;
  font-size: 22px;
  font-weight: 500;
}

.weather-details p {
  color: green;
  font-size: 14px;
  font-weight: 500;
}

.not-found {
  width: 100%;
  text-align: center;
  opacity: 0;
  margin-top: 50px;
  display: none;
  scale: 0;
}

.not-found img {
  width: 60%;
}

.not-found p {
  color: green;
  margin-top: 12px;
  font-size: 22px;
  font-weight: 500;
}

.weather-box, .weather-details {
  opacity: 0;
  scale: 0;
}

.fadeIn {
  animation: .5s .5s forwards fadeIn;
}

@keyframes fadeIn {
  to {
    opacity: 1;
    scale: 1;
  }
}

